import React, { useState } from "react";
import PersonList from "./components/PersonList";
import PersonForm from "./components/PersonForm";
import PersonSearch from "./components/PersonSearch";
import { getAllPersons } from "./api/personApi";
import { Container, Typography, Box, Paper } from "@mui/material";

const App = () => {
  const [persons, setPersons] = useState([]);
  const [editingPerson, setEditingPerson] = useState(null);

  const refreshList = async () => {
    const res = await getAllPersons();
    setPersons(res.data);
  };

  return (
    <Container sx={{ marginTop: 4, marginBottom: 4 }}>
      <Typography variant="h3" gutterBottom align="center" sx={{ color: "#1976d2" }}>
        Person Management
      </Typography>

      <Paper sx={{ padding: 4, maxWidth: 500, margin: "0 auto", backgroundColor: "#f5f5f5" }}>
        <PersonForm
          onPersonAdded={refreshList}
          editingPerson={editingPerson}
          setEditingPerson={setEditingPerson}
        />
      </Paper>

      <Box sx={{ marginY: 4 }}>
        <PersonSearch onSearchResult={setPersons} />
      </Box>

      <PersonList
        persons={persons}
        refreshList={refreshList}
        setEditingPerson={setEditingPerson}
      />
    </Container>
  );
};

export default App;
