import React, { useState } from "react";
import { getPersonById, searchPersonsByName } from "../api/personApi";
import { TextField, Button, Box } from "@mui/material";

const PersonSearch = ({ onSearchResult }) => {
  const [name, setName] = useState("");
  const [id, setId] = useState("");

  const handleSearchByName = async () => {
    const res = await searchPersonsByName(name);
    onSearchResult(res.data);
  };

  const handleSearchById = async () => {
    if (!id) return;
    try {
      const res = await getPersonById(id);
      onSearchResult([res.data]);
    } catch {
      onSearchResult([]);
      alert("Person not found");
    }
  };

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, maxWidth: 500, margin: "0 auto" }}>
      <Box sx={{ display: 'flex', gap: 1 }}>
        <TextField placeholder="Search by Name" fullWidth value={name} onChange={(e) => setName(e.target.value)} />
        <Button variant="contained" color="primary" onClick={handleSearchByName}>Search</Button>
      </Box>
      <Box sx={{ display: 'flex', gap: 1 }}>
        <TextField placeholder="Search by ID" fullWidth value={id} onChange={(e) => setId(e.target.value)} />
        <Button variant="contained" color="secondary" onClick={handleSearchById}>Search</Button>
      </Box>
    </Box>
  );
};

export default PersonSearch;
